import Ajax from 'core/ajax';
import {getPluginOptionName} from 'editor_tiny/options';
import {component} from './common';

const METHOD = 'tiny_acessibilia_get_config';

export const applyServerConfig = async (editor) => {
  const call = Ajax.call([{methodname: METHOD, args: {}}])[0];
  const cfg = await call;

  // Converte chaves em namespaced e seta no editor.
  Object.entries(cfg).forEach(([k, v]) => {
    const full = getPluginOptionName(component, k); // tiny_acessibilia:k
    editor.options.set(full, v);
  });

  // (Opcional) log r√°pido:
  console.log('[acessibilia] config bridged', cfg);
};
